body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  /* background-color: #141414; */
  /* font-family: 'Anek Malayalam', sans-serif; */
  /* font-family: 'Courier New', Courier, monospace; */
  font-family: 'Roboto', sans-serif;
  background: url(/public/images/background.png);
  background-size: cover;
  background-position: right;
}

.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
.calculator{
  width: 320px;
  height: 600px;
  background-color: #191919;
  margin-top: 3%;
  border-radius: 25px;
}
.input-section{
  width: 320px;
  height: 15%;
  
}
.result-section{
  width: 320px;
  height: 15%;
  
}

.input-section input{
  width: 316px;
  height: 100%;
  border: none;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  background-color: #191919;
  box-shadow: 0px 9px 6px rgba(0, 0, 0, 0.25);
  color: #a160fb;
  font-size: 28px;
  text-align: right;
  padding: 35px 0px;
}
.result-section input{
  text-align: right;
  width: 316px;
  height: 100%;
  border: none;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: #191919;
  box-shadow: 0px 9px 6px rgba(0, 0, 0, 0.25);
  color: white;
  font-size: 45px;
}
.input-section input:focus{
  outline: none;
}
.result-section input:focus{
  outline: none;
}
.input-btns{
  display: flex;
  flex-direction: row;
  grid-gap: 21px;
  gap: 21px;
  flex-wrap: wrap;
  padding: 15px;
  margin-left: 5px;
  margin-top: 10px;
}
.input-btns .btn{
  width: 55px;
  height: 55px;
  border-radius: 8px;
  background: #171717;
  border: none;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25), -2px -2px 4px rgba(255, 255, 255, 0.1);
  
}
.input-btns button{
  background: none;
  width: 100%;
  height: 100%;
  border-radius: 8px;
  
  color: white;
  font-size: 30px;
  border: none;
}
.fun-btn{
  width: 55px;
  height: 55px;
  border-radius: 8px;
  background: #171717;
  border: none;
  font-size: 30px;
}
.equal-btn{
  width: 55px;
  height: 55px;
  border-radius: 8px;
  background: #171717;
  border: none;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25), -2px -2px 4px rgba(255, 255, 255, 0.1);
  
  background-color: #a160fb;
}
